.info {
	display: flex;
	@include md {
		width: 290px;
		margin: 0 auto; }

	&__icon {
		font-size: 82px;
		//+tablet
		//ont-size: 60px
		@include md {
			font-size: 51px;
			display: flex; }

		.icon {
			fill: $yellow; }
		&:not(:last-child) {
			margin-right: 30px;
			@include md {
				margin-right: 19px; } } }
	&__content {
		padding-top: 5px;
		@include md {
			padding-top: 0;
			margin-top: -3px; } }
	&__title {
		color: $yellow;
		@include gotBlack;
		font-size: 30px;
		letter-spacing: 0.11px;
		line-height: 1;
		@include md {
			font-size: 20px; }
		&:not(:last-child) {
			margin-bottom: 9px;
			@include md {
				margin-bottom: 0; } } }
	&__descr {
		font-size: 20px;
		line-height: 1.2;
		@include md {
			font-size: 14px;
			line-height: 1.3; } } }




.info-wrapper {
	padding: 42px 0 24px;
	@include md {
		padding: 24px 0; }
	&__inner {
		display: flex;
		justify-content: center;
		@include xs {
			flex-direction: column;
			align-items: center; }
		.info {
			&:not(:last-child) {
				margin-right: 106px;
				@include sm {
					margin-right: 50px; }
				@include xs {
					margin: 0 0 20px; } } } } }
