.nominations {
	padding: 26px 0 137px;
	@include tablet {
		padding: 20px 0 119px; }
	@include md {
		padding: 20px 0 88px; }
	&__inner {}
	&__title {
		color: $red;
		@include gotBlack;
		font-size: 60px;
		line-height: 1.2;
		letter-spacing: 0.3px;
		text-align: right;
		padding-right: 90px;
		margin-bottom: -62px;
		@include tablet {
			font-size: 36px;
			padding-right: 73px;
			margin-bottom: -36px; }
		@include md {
			font-size: 32px;
			text-align: left;
			margin-bottom: 20px; } }
	&__content {
		padding-left: 50px;
		margin-bottom: 147px;
		@include tablet {
			padding: 0 8px 0 34px; }
		@include md {
			padding: 0;
			margin-bottom: 70px; }
		@include sm {
			margin-bottom: 30px; } }
	&__item {
		width: 44.6%;
		max-width: 549px;
		position: relative;
		@include tablet {
			width: 47.5%; }
		@include sm {
			width: 100%;
			max-width: 100%;
			margin: 0 auto; }
		&:not(:last-child) {
			margin-bottom: -15px;
			@include tablet {
				margin-bottom: -33px; }
			@include sm {
				margin-bottom: 10px; } }
		&:nth-child(2n) {
			margin-left: auto;
			margin-bottom: 7px;
			@include tablet {
				margin-bottom: 0; } }
		&:last-child {
			margin-bottom: 0; } }
	&__btn {
		.btn {
			text-transform: uppercase;
			max-width: 490px;
			margin: 0 auto;
			padding: 24px 31.5px;
			@include tablet {
				max-width: 274px;
				padding: 15px 10px;
				font-size: 16px;
				margin: 0 auto; }
			@include md {
				max-width: 47.5%; }
			@include sm {
				max-width: 100%;
				padding: 8px 15px; }
			@include xs {
				width: 100%;
				padding: 8px 20px; }
			&_red {
				color: $red; } }
		@include tablet {
			margin-top: 0; }
		@include md {
			margin-top: 30px!important;
			padding-left: 9px; } }

	&__decor {
		position: absolute;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		top: 10px;
		left: -11px;
		border: 4px solid $red;
		background-color: $white;
		&_7 {
			top: 20px;
			left: -21px; }
		&_6 {
			top: 31px;
			left: -32px; }
		&_5 {
			top: 41px;
			left: -42px; }
		&_4 {
			top: 51px;
			left: -52px; }
		&_3 {
			top: 61px;
			left: -62px; }
		&_2 {
			top: 71px;
			left: -72px; }
		&_1 {
			top: 80px;
			left: -82px; }
		&_tablet-dn {
			@include tablet {
				display: none; } }
		&_tablet-db {
			display: none;
			@include tablet {
				display: block; } }
		@include md {
			display: none; } } }



.nomination {
	position: relative;
	padding: 41px 20px;
	border: 4px solid $red;
	border-top-width: 44px;
	display: flex;
	justify-content: center;
	z-index: 2;
	background-color: $white;

	@include tablet {
		border-top-width: 35px;
		padding: 33px 20px; }
	@include md {
		border-top-width: 30px;
		padding: 24px 12px; }
	//&:before
	//osition: absolute
	//ontent: ''
	//isplay: block
	//idth: 100%
	//eight: 40px
	//ackground-color: $red
	//op: 0
	//eft: 0
	&__text {
		color: $red;
		text-transform: uppercase;
		@include bold;
		font-size: 24px;
		line-height: 1.5;
		@include tablet {
			font-size: 18px; }
		@include md {
			font-size: 16px; }
		@include sm {
			text-align: center; } } }

