.text-content {
	padding: 163px 0 36px;
	color: $red;
	@include md {
		padding: 83px 0 55px; }
	&__inner {
		max-width: 879px;
		.text {
			&:not(:last-child) {
				margin-bottom: 147px;
				@include tablet {
					margin-bottom: 100px; }
				@include md {
					margin-bottom: 80px; }
				@include sm {
					margin-bottom: 32px; } } } } }



.text {
	&__title {
		@include gotBlack;
		font-size: 57px;
		@include sm {
			font-size: 32px; }
		&:not(:last-child) {
			margin-bottom: 57px;
			@include tablet {
				margin-bottom: 40px; }
			@include md {
				margin-bottom: 32px; } } }
	&__descr {
		a {
			color: inherit;
			word-break: break-all;
			@include hover {
				text-decoration: none; } }
 } }		//letter-spacing: 0.28px
