.schedule {
	padding-top: 120px;
	@include tablet {
		padding-top: 72px; }
	@include md {
		padding-top: 32px; }
	&__title {
		color: $red;
		margin-bottom: 60px;
		@include tablet {
			margin-bottom: 46px; }
		@include md {
			margin-bottom: 38px; }
		@include xxs {
			padding: 0 14px; } }
	&> .wrapper {
		@include xxs {
			padding: 0; } } }




.tabs {
	&__header {
		display: flex;
		justify-content: space-between;
		margin-bottom: 30px;
		@include tablet {
			margin-bottom: 19px; }
		@include md {
			display: block;
			margin-bottom: 29px; }
		@include xxs {
			padding: 0 14px; }
		li {
			width: 100%;
			max-width: 31.56%;
			&:not(:last-child) {
				@include md {
					margin-bottom: 20px; } }
			@include tablet {
				max-width: 33%; }
			@include md {
				max-width: 100%; }
			@include sm {
				margin-bottom: 28px; }
			.btn {
				color: $red;
				padding-top: 23px;
				padding-bottom: 23px;
				text-transform: uppercase;
				@include tablet {
					font-size: 16px;
					padding: 17px 10px; }
				@include md {
					padding-top: 10px;
					padding-bottom: 10px; }
				&.is-active {
					background-color: $red;
					color: $white;
					pointer-events: none;
					&:before {
						display: none; } } } } }
	&__content {
		.table {
			&:not(.is-active) {
				display: none; } } } }

