.hero {
	//min-height: 1033px
	min-height: 100vh;
	background-size: cover;
	background-position: center bottom;
	background-repeat: no-repeat;
	padding: 100px 20px 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	@include tablet {
		padding: 94px 20px 0; }
	@include md {
		padding: 54px 20px 0; }
	@include xs {
		padding: 45px 20px 0; }
	&__logo {
		margin-bottom: 43px;
		@include md {
			margin-bottom: 57px; }
		//+xsh
		//argin-bottom: 15px!important
		@include xs {
			margin-bottom: 30px; } }
	&__title {
		max-width: 1325px;
		text-align: center;
		@include gotBlack;
		font-size: 90px;
		line-height: 1.2;
		letter-spacing: 0.25px;
		display: block;
		&:not(:last-child) {
			margin-bottom: 34px;
			@include tablet {
				margin-bottom: 17px; }
			@include md {
				margin-bottom: 21px; } }
		@include tablet {
			font-size: 56px;
			margin-bottom: 22px;
			letter-spacing: 2.7px; }
		@include md {
			font-size: 38px;
			line-height: 1.15;
			margin-bottom: 16px;
			letter-spacing: 0.25px; }
		//+xsh
		//ont-size: 22px!important
		@include xs {
			font-size: 28px;
			margin-bottom: 20px;
			line-height: 1.34; } }
	&__descr {
		max-width: 900px;
		text-align: center;
		font-size: 26px;
		line-height: 1.45;
		margin-bottom: 110px;
		@include tablet {
			max-width: 625px;
			font-size: 18px;
			margin-bottom: 113px;
			line-height: 1.5; }
		@include md {
			font-size: 14px;
			line-height: 1.6;
			margin-bottom: 98px; }
		//+xsh
		//ont-size: 12px!important
 }		//argin-bottom: 15px!important
	&__btn {
		@include md {
			//padding: 0 10px
			.btn {
				min-width: 247px;
				width: 100%; } } }
	&_min {
		min-height: auto;
		padding: 110px 0 100px;
		background-position: center;
		@include tablet {
			padding: 94px 20px; }
		@include md {
			padding: 44px 20px; }
		@include xs {
			padding: 44px 20px; }
		.hero {
			&__logo {
				margin-bottom: 60px;
				@include md {
					margin-bottom: 20px; } }
			&__title {
				font-size: 80px;
				max-width: 836px;
				margin: 0 auto;
				line-height: 1.1;
				@include tablet {
					font-size: 60px; }
				@include md {
					font-size: 44px; }
				@include sm {
					font-size: 32px; } } } } }

.intro {
	z-index: 1;
	position: relative; }
